<script>
import Carousel from "../components/Carousel.vue";

export default {
  name: "About",
  components: {
    Carousel,
  },
  data() {
    return {
      show: false,
      show2: false,
      lastScrollPosition: 0,
      slides: [
        require("../assets/img/bg-1.jpg"),
        require("../assets/img/bg-2.jpg"),
        require("../assets/img/bg-3.jpg"),
        require("../assets/img/bg-video.png"),
      ],
    };
  },
  mounted() {
    window.addEventListener("scroll", this.slide);
  },
  methods: {
    slide() {
      const currentScrollPosition = window.pageYOffset;
      if (currentScrollPosition > 100 && currentScrollPosition < 399) {
        console.log(currentScrollPosition);
        this.show = true;
      }
      if (currentScrollPosition > 400 && currentScrollPosition < 800) {
        this.show2 = true;
        console.log(currentScrollPosition);
      }
      if (Math.abs(currentScrollPosition - this.lastScrollPosition)) {
        return;
      }
      this.show = currentScrollPosition > this.lastScrollPosition;
      this.show2 = currentScrollPosition > this.lastScrollPosition;

      this.lastScrollPosition = currentScrollPosition;
    },
  },
};
</script>

